<script>
import { useI18n } from 'vue-i18n'
import { useRouter } from 'vue-router'
import { computed } from 'vue'
import useDate from '@/utils/useDate'
import Icon from '@/components/Icon/Icon'

export default {
  name: 'Sidebar',
  components: { Icon },
  setup () {
    const { t } = useI18n()
    const router = useRouter()
    const linkClick = item => {
      if (item.to) {
        router.push(item.to)
      }
    }

    const autoLogout = useDate(Date.now(), 'HH:MM')

    const main = computed(() => ([
      { title: t('sidebar.dashboard'), key: 'dashboard', to: '/', icon: 'dashboard', count: 3 },
      { title: t('sidebar.transactions'), key: 'transactions', to: '/', icon: 'transactions', count: 11 },
      { title: t('sidebar.news'), key: 'news', to: '/', icon: 'news', count: 0 }
    ]))

    const settings = computed(() => ([
      { title: t('sidebar.settings.general'), key: 'general', to: '/', count: 3 },
      { title: t('sidebar.settings.users'), key: 'users', to: '/', count: 3 },
      { title: t('sidebar.settings.apiKeys'), key: 'apiKeys', to: '/', count: 3 },
      { title: t('sidebar.settings.statusPage'), key: 'statusPage', to: '/', count: 3 },
      { title: t('sidebar.settings.logs'), key: 'logs', to: '/', count: 3 }
    ]))

    const treasury = computed(() => ([
      { title: t('sidebar.treasury.bank'), key: 'bank', to: '/', count: 3 },
      { title: t('sidebar.treasury.withdrawals'), key: 'withdrawals', to: '/', count: 3 },
      { title: t('sidebar.treasury.deposits'), key: 'deposits', to: '/', count: 3 }
    ]))

    return {
      t,
      main,
      settings,
      treasury,
      autoLogout,
      linkClick
    }
  }
}
</script>

